import {StoryComment} from "./comment.model";

export class Story {
  public id: number;
  public createdDate: string;
  public storyTitle: string;
  public storyHighlight: string;
  public mainStory: string;
  public imagePath: string;
  public comments: StoryComment[];

  constructor(id, storyTitle, storyHighlight, createdDate, mainStory, imagePath) {
    this.id = id;
    this.createdDate = createdDate;
    this.storyTitle = storyTitle;
    this.storyHighlight = storyHighlight;
    this.mainStory = mainStory;
    this.imagePath = imagePath;

  }
  addComments(stroyComment: StoryComment) {
    this.comments.push(stroyComment)
  }
}
