import {Component, Input, OnInit} from '@angular/core';
import {Story} from '../story.model';
import {BehaviorSubject, Observable, Subject} from 'rxjs';
import {ActivatedRoute, Params} from '@angular/router';
import {routeAnimation} from '../../page-switch-animation';
import {DataService} from '../data.service';
import {StoryComment} from "../comment.model";

@Component({
  selector: 'app-story',
  templateUrl: './story.component.html',
  styleUrls: ['./story.component.scss'],
  animations: [
    routeAnimation
  ]
})
export class StoryComponent implements OnInit {
  @Input() newComment;
  story: Story;
  private storyComments: StoryComment[];
  storyCommentChanged = new Subject<StoryComment[]>()
  private id: number;

  constructor(
    private route: ActivatedRoute,
    private dataService: DataService) {
  }
  ngOnInit() {
    const id = this.route.params.subscribe(
      (params: Params) => {
        this.id = params.id;
        this.dataService.fetchStory(this.id).toPromise<Story>(
        ).then(
          (data
          ) => {
            this.story = data;
            this.storyComments = data.comments;
            this.storyCommentChanged.next(data.comments);
          });
      }
    );
    this.storyCommentChanged.subscribe(
      (storyComments: StoryComment[]) => {
        this.storyComments = storyComments;
      }
    );

  }
  receiveNewComment(storyComment:StoryComment) {
      this.storyComments.splice(0,0,storyComment);
      this.storyCommentChanged.next(this.storyComments);
  };
}
