import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {BasicProfileComponent} from './profile/basic-profile/basic-profile.component';
import {ExperianceComponent} from './profile/expriance/experiance.component';
import {EducationComponent} from './profile/education/education.component';
import {ActivitiesComponent} from './profile/activities/activities.component';
import {ProfileComponent} from './profile/profile.component';
import {NavigatorComponent} from './profile/navigator/navigator.component';
import {BlogComponent} from './blog/blog.component';
import {ContactMeComponent} from './contact-me/contact-me.component';
import {CoverStoryComponent} from './blog/cover-story/cover-story.component';
import {StoryComponent} from './blog/story/story.component';


const routes: Routes = [
  {path: '', redirectTo: 'blog', pathMatch: 'full'},
  {
    path: 'blog', component: BlogComponent, children: [
      {path: '', component: CoverStoryComponent, pathMatch: 'full'},
      {path: ':id', component: StoryComponent, pathMatch: 'full'}
    ]
  },
  {path: 'contact-me', component: ContactMeComponent, pathMatch: 'full'},
  {
    path: 'profile', component: ProfileComponent, children: [
      {path: 'basic-profile', component: BasicProfileComponent, pathMatch: 'full'},
      {path: 'experience', component: ExperianceComponent, pathMatch: 'full'},
      {path: 'education', component: EducationComponent, pathMatch: 'full'},
      {path: 'activities', component: ActivitiesComponent, pathMatch: 'full'}
    ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {onSameUrlNavigation: 'reload'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }

export const routingComponent = [
  ActivitiesComponent,
  NavigatorComponent,
  BasicProfileComponent,
  ExperianceComponent,
  EducationComponent,
  ProfileComponent,
  BlogComponent,
  ContactMeComponent
];
