import {Injectable, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Story} from './story.model';
import {StoryComment} from "./comment.model";
import {environment} from "../../environments/environment";



@Injectable()
export class DataService {

  apiUrl = environment.baseUrl +'blog/';

  constructor(
    private httpClient: HttpClient,
  ) {
  }


  fetchStories(): any {
    return this.httpClient.get<Story[]>(this.apiUrl + 'all-stories/');
  }

  fetchStory(id: number) {
    return this.httpClient.get<Story>(this.apiUrl + id + '/')
  }
  addComment(storyId: number, storyComment: StoryComment){
    return this.httpClient.post<StoryComment>(this.apiUrl + 'add-comment/',  {
      postId:storyId,
      firstName: storyComment.firstName,
      lastName: storyComment.lastName,
      comment: storyComment.comment,
      email: storyComment.email
    })
  }
  fetchCommentForPost(storyId: number){
    return this.httpClient.get<Story>(this.apiUrl + storyId + '/all_comments');
  }
}
